@font-face {
  font-family: "Roboto", sans-serif;
  src: url("../fonts/Roboto.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  src: url("../fonts/Robotobold.ttf") format("truetype");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.display-none {
  display: none;
}
.container {
  width: 1200px;
  margin: 0 auto;
}
.button {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  display: block;
  color: #ffffff;
  text-decoration: none;
  border: none;
  background: #000000;
  padding: 15px 5px;
  width: 100%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  transition: all 0.3s;
}
.button:hover {
  border-radius: 15px;
}
.title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 60px;
  line-height: 70px;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}
.main-page {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}
.main-container__title {
  text-align: center;
  margin-bottom: 70px;
}
.main-title-span {
  color: #008000;
}
.main-header {
  border-bottom: 1px solid #e5e5e5;
  padding: 25px 0;
}
.main-header__container {
  display: flex;
  align-items: center;
}
.main-header__logo-container {
  margin-right: 244px;
}
.main-nav__list {
  width: 515px;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.main-nav__link {
  font-size: 15px;
  line-height: 18px;
  text-decoration: none;
  color: #030305;
}
.presenting-page {
  background: url("../img/presenting-page-background.png");
  padding: 105px 0 154px 0;
  margin-bottom: 100px;
}
.presenting-page__title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 80px;
  line-height: 110%;
}
.presenting-page__text {
  font-size: 16px;
  line-height: 130%;
  width: 502px;
  margin: 0;
  margin-bottom: 40px;
}
.presenting-page__link {
  width: 334px;
}
.our-parking__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 384px);
  gap: 40px 24px;
}
.our-parking {
  margin-bottom: 100px;
}
.car-card__img {
  margin-bottom: 15px;
  vertical-align: bottom;
  transition: all 0.3s;
}
.car-card__img:hover {
  transform: scale(1.1);
}
.car-card__title {
  margin: 0;
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  letter-spacing: 0.02em;
}
.car-card__description-container {
  display: grid;
  grid-template-columns: repeat(3, 110px);
  gap: 15px;
  margin-bottom: 15px;
}
.car-characteristic {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.car-characteristic__img {
  margin-bottom: 10px;
}
.car-characteristic__title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
}
.car-characteristic__text {
  margin: 0;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
}
.buy-form {
  position: relative;
  padding-bottom: 121px;
}
.buy-form__bg {
  position: absolute;
  width: 969px;
  height: 506px;
  background: url("../img/rolls.png") no-repeat;
  z-index: -1;
  right: -600px;
  bottom: 0;
}
.buy-form__title {
  margin-bottom: 20px;
}
.buy-form__text {
  margin: 0;
  margin-bottom: 60px;
  font-size: 16px;
  line-height: 130%;
  color: #5d5d5f;
}
.buy-form__form {
  width: 334px;
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
}
.buy-form__input {
  padding: 22px 18px;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
}
.buy-form__input::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #5d5d5f;
}
.buy-form__input:hover {
  outline: 1px solid #000000;
}
.buy-form__submit {
  padding: 20px 5px;
}
.main-footer {
  border-top: 1px solid #e5e5e5;
  padding: 25px 0;
}
.main-footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-footer__text {
  font-size: 15px;
  line-height: 18px;
}
